	/**
	 * 
	 * 机构柜员相关操作及方法
	 */

	postChange(node,event,id) {
		if(event) {
			this.postIdList.push(node.postId);
			this.postList.push({postId:node.postId,postName:node.postName});
		}else{
			let indexof = this.postIdList.indexOf(id);
			indexof!=-1?this.postIdList.splice(indexof,1):this.postIdList;
			for(let i=0;i<this.postList.length;i++){
				if(this.postList[i].postId == id){
					this.postList.splice(i,1);
					break;
				}
			}
		}
		console.log(this.postList);
	}
	//勾选柜员查岗位（初次查询界面 已更新）
	choicePeople(col) {
		this.checkValue1 = [];
		this.checkValues = [];
		this.postIdList=[];
		this.postList=[];
		this.postLists=[];
		this.postOrgPositionBean.partyId = col.orgId;
		this.postOrgPositionBean.userLoginId = col.tellerId;
		this.postuserName = col.tellerName;
		this.peopleMessage = col;
		this.orgName = this.firstOrgName;
		this.commonHttpService.queryPostBytellerIdAndOrgIdLevel(this.postOrgPositionBean).subscribe(data => {
			if (data.returnCode.code == 'success') {
				if (data.postInfoListForYY.length) {
					for (let i of data.postInfoListForYY) {
						if (i.exitFlag == '0') {
							this.checkValue1.push(i.postId);
							console.log(this.checkValue1)
							this.postIdList.push(i.postId);
							this.postList.push({postId:i.postId,postName:i.postName});
						}
					}

					this.checkValues = _.clone(this.checkValue1);
					this.postLists=_.clone(this.postList);
				}
				let params = [
					{
						postId: "",
						postName: "当前岗位",
						children: data.postInfoListForYY,
					},
					{
						postId: "",
						postName: "总行层级岗位",
						children: data.postInfoListForJt,
					}, {
						postId: "",
						postName: "分行及支行层级岗位",
						children: data.postInfoListForFh,
					},
				];
				this.positionTableData = this.toTree(params);
				// this.isUnfold=true;
				this.positionTableData.forEach(node => {
					this.expandedClick(node, true);//默认岗位的树是展开的
				});


				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: '查询机构岗位成功' });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})
	}

