	//加入权限回调 已更新 
	/**
	 * 
	 * @param event 加入权限回调的时候岗位列表重新查询 ?? 
	 * 打印看下第一次勾选存的postOrgPositionBean还有没有值
	 */
	addPowerCall(event) {
		this.checkValue1 = [];
		this.checkValues = [];
		this.postIdList=[];
		this.postList=[];
		this.postLists=[];
		this.display = false;
		this.postOrgPositionBean.partyId = event.orgId;
		this.partyId = event.orgId;
		this.orgName = event.orgName;
		this.partyName = event.orgName;
		this.commonHttpService.queryPostInfoByPostTeller(this.postOrgPositionBean).subscribe(data => {
			if (data.returnCode.code == 'success') {
				// this.postQuery();
				let param = {
					partyId: this.postOrgPositionBean.partyId,
					userLoginId: this.postOrgPositionBean.userLoginId
				}
				this.queryClick(param);

				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: '查询机构岗位成功' });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})
	}
	queryClick(col) {
		this.checkValue1 = [];
		this.checkValues = [];
		this.postIdList=[];
		this.postList=[];
		this.postLists=[];
		this.postOrgPositionBean.partyId = col.partyId;
		this.postOrgPositionBean.userLoginId = col.userLoginId;
		this.postuserName = col.userName;
		this.commonHttpService.queryPostBytellerIdAndOrgIdLevel(this.postOrgPositionBean).subscribe(data => {
			if (data.returnCode.code == 'success') {
				for (let i of data.postInfoListForYY) {
					if (i.exitFlag == '0') {
						this.checkValue1.push( i.postId);
						this.postIdList.push(i.postId);
						this.postList.push({postId:i.postId,postName:i.postName});
						
					}
				}
				this.checkValues = _.clone(this.checkValue1);
				this.postLists = _.clone(this.postList);
				let params = [
					{
						postId: "",
						postName: "当前岗位",
						children: data.postInfoListForYY,
					}, {
						postId: "",
						postName: "总行层级岗位",
						children: data.postInfoListForJt,
					}, {
						postId: "",
						postName: "分行及支行层级岗位",
						children: data.postInfoListForFh,
					},
				];
				this.positionTableData = this.toTree(params);
				// this.isUnfold=true;
				this.positionTableData.forEach(node => {
					this.expandedClick(node, true);//默认岗位的树是展开的
				});
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: '查询机构岗位成功' });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})
	}

