	//保存权限
	//partyId机构标识   userLoginId人员标识  postIdList岗位标识
	savePower() {
		if (this.queryQuick1) {
			this.positionParam = {
				postIdList: this.postIdList,
				partyId: this.postOrgPositionBean.partyId,
				userLoginId: this.peopleMessage.userLoginId
			}
		} else {
			this.positionParam = {
				postIdList: this.postIdList,
				partyId: this.postOrgPositionBean.partyId,
				userLoginId: this.postOrgPositionBean.userLoginId
			};
		}
		if (this.positionParam.postIdList.length != 0 && this.positionParam.userLoginId) {
			this.commonHttpService.updateOrgPostTellerRelation(this.positionParam).subscribe(data => {
				if (data.returnCode.code == 'success') {
					this.createOperateRecode();
					this.queryClick(this.positionParam);
					this.msgs = [];
					this.msgs.push({ severity: 'success', summary: '提示', detail: '保存成功' });
				} else {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
				}
			}, error => {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
			})
			this.partyName = this.firstOrgName;
			this.partyId = this.firstOrgId;
		} else {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '机构柜员或岗位不能为空' })
		}

	}
