	createOperateRecode() {
		let lastRecode = {};//原信息
		let nowRecode = {};//现信息

		if (this.queryQuick1) {
			lastRecode = {
				tellerId: this.peopleMessage.userLoginId,
				tellerName: this.peopleMessage.userName,
				postList: this.postLists,
				orgId: this.peopleMessage.partyId,
				orgName: this.peopleMessage.partyName,
				operateTellerId:this.creat_user,
				operateTellerName:this.userName,
			};
			nowRecode = {
				tellerId: this.peopleMessage.userLoginId,
				tellerName: this.peopleMessage.userName,
				postList: this.postList,
				orgId: this.peopleMessage.partyId,
				orgName: this.peopleMessage.partyName,
				operateTellerId:this.creat_user,
				operateTellerName:this.userName,
			};
		} else {
			lastRecode = {
				tellerId: this.postOrgPositionBean.userLoginId,
				tellerName: this.postuserName,
				postList: this.postLists,
				orgId: this.partyId,
				orgName: this.partyName,
				operateTellerId:this.creat_user,
				operateTellerName:this.userName,
			};
			nowRecode = {
				tellerId: this.postOrgPositionBean.userLoginId,
				tellerName: this.postuserName,
				postList: this.postList,
				orgId: this.partyId,
				orgName: this.partyName,
				operateTellerId:this.creat_user,
				operateTellerName:this.userName,
				
			};
		}

		let Param = {
			externalIdType: 'POST',
			fromOperateRecode: JSON.stringify(lastRecode),
			toOperateRecode: JSON.stringify(nowRecode),
			applyDate:this.commfunc.getNowFormatDate(),//获取当前时间
		};
		this.commonHttpService.createOperateRecode(Param).subscribe(data => {
			if (data.returnCode.code == 'success') {

			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})
	}
