import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { PostAssignmentBean } from '../bean/post-assignment.bean';
import { CommonHttpService } from '../../../http/common.http.service';
import { Message, CheckboxModule } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { TZB_COMMON_DOWNLOAD, TZB_COMMON_UPLOAD, TZB_CUS_UPLOADBZ } from 'app/@uisftech/common/service/http.constant';

@Component({
  selector: 'add-moules-filer',
  templateUrl: './add-moules-filer.component.html',
  styleUrls: ['./add-moules-filer.component.scss']
})
export class AddMoulesFilerComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  UPLOAD: string;//上传
  fileList: any = [];//上传文件列表
  msgs: Message[];
  DOWNLOAD: string = TZB_COMMON_DOWNLOAD;//下载
  files: any[] = [];

  constructor(
    public commonHttpService: CommonHttpService,
    public commfunc: Commfunc,
  ) { }

  ngOnInit() {
    this.UPLOAD = TZB_COMMON_UPLOAD + '/?path=upload';
  }
  load(event) {
    for (let file of event.files) {
      this.files = [];
      this.files.push(file)
    }
    if (event.xhr.response) {
      this.fileList = JSON.parse(event.xhr.response);
      if(this.fileList.hasOwnProperty('errorMsg')){
        this.msgs = [{severity:'error',summary:'提示',detail:this.fileList.errorMsg}];
      }
    }
    if (this.fileList.length > 0) {
      let param = {};
      param = {
        localPath: this.fileList[0].filePath,
        fileName: this.fileList[0].fileName,
      }
      this.commonHttpService.uploadPostInfo(param).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
        } else {
          data.returnCode.code ? data.returnCode.code : '调用服务失败';
          this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
        }
      });
    }
  }
  //确认
  query() {
    let parems = {

    };
    this.outValue.emit(parems);
  }
}
