import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { PostAssignmentBean } from '../bean/post-assignment.bean';
import { CommonHttpService } from '../../../http/common.http.service';
import { Message, CheckboxModule } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
  selector: 'app-quick-query-message',
  templateUrl: './quick-query-message.component.html',
  styleUrls: ['quick-query-manage.scss']
})
export class QuickQueryMessageComponent implements OnInit {
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  postAssignmentBean: PostAssignmentBean = new PostAssignmentBean();
  tableDataWithParty: any;//接收表格数据
  msgs: Message[] = [];  //提示信息
  total: string;//接收总条数
  inpFlag: boolean = true;
  orgIdList: any[] = [];
  orgNameList: any[] = [];
  orgId: string;
  orgName: string;
  constructor(
    public commonHttpService: CommonHttpService,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {
    console.log(this.inValue);
    this.postAssignmentBean.pageNum = '1';
    this.postAssignmentBean.pageSize = '10';
    this.orgquery('');
  }

  inpquery(event) {
    if (this.inpFlag) {
      this.inpFlag = false;
      setTimeout(() => {
        this.inpFlag = true;
        let orgContent = event.srcElement.value
        this.orgquery(orgContent)
      }, 1000)
    }
  }
  //机构模糊查询
  orgquery(orgContent) {
    // this.orgNameList = [
    //   // { label: '请选择', value: '' },
    //   { label: this.inValue.label, value: this.inValue.value },
    // ];
    // this.orgIdList = [
    //   // { label: '请选择', value: '' },
    //   { label: this.inValue.value, value: this.inValue.label },
    // ];
    // this.orgId = this.orgIdList[0].value;
    // this.orgName = this.orgNameList[0].value;
    // this.postAssignmentBean.orgId = this.orgIdList[0].label;
    // this.postAssignmentBean.orgName = this.orgNameList[0].label;
    let param = {
      orgId: /\w/.test(orgContent) ? orgContent : "",
      orgName: /\w/.test(orgContent) ? "" : orgContent,
      pageSize: 30,
      OrgStatus: '0'
    }
    this.commonHttpService.queryVagueOrgList(param).subscribe(data => {
      if (data.orgList) {
        this.orgNameList = this.commfunc.listToLabelvalue(data.orgList, 'orgName', 'orgId');
        this.orgIdList = this.commfunc.listToLabelvalue(data.orgList, 'orgId', 'orgName');
      }
    })
  }
  orgIdChange() {
    this.exchange(this.orgIdList)
  }
  orgNameChange() {
    this.exchange(this.orgNameList)
  }
  exchange(list) {
    for (let i = 0; i < list.length; i++) {
      if (/\w/.test(list[0].label)) {
        if (list[i].value == this.orgId) {
          this.postAssignmentBean.orgId = list[i].label;
        }
      } else {
        if (list[i].value == this.orgName) {
          this.postAssignmentBean.orgName = list[i].label;
        }
      }
    }
  }
  //快速查询确定按钮 已更新
  confirmQuery() {
    let event = {
      a: false,
      b: this.postAssignmentBean
    }
    this.outValue.emit(event);
  }
  // confirmQuery() {
  //   this.commonHttpService.queryOrgTellerInfoByPostOrTeller(this.postAssignmentBean).subscribe(data => {
  //     if (data.returnCode.code = 'success') {
  //       this.tableDataWithParty = data.orgTellerList
  //       this.total = data.total
  //       let event = {
  //         a:this.tableDataWithParty,
  //         b:this.total
  //       }

  //       this.outValue.emit(event);
  //     } else {
  //       this.msgs = [];
  //       this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
  //     }
  //   }, error => {
  //     this.msgs = [];
  //     this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
  //   })
  // }
}
