import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { PostAssignmentBean } from '../bean/post-assignment.bean';
import { CommonHttpService } from '../../../http/common.http.service';
import { Message, CheckboxModule } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-add-power-window',
  templateUrl: './add-power-window.component.html',
  styleUrls: ['./add-power-window.component.scss']
})
export class AddPowerWindowComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;
  postAssignmentBean: PostAssignmentBean = new PostAssignmentBean();
  tableData: any;//接收表格数据
  msgs: Message[] = [];  //提示信息
  list: any = [];//机构模糊查询的下拉框
  list1: any = [];//机构模糊查询的下拉框
  orgName:string;//需要的机构号
  orgId:string;//需要的机构名称
  inpFlag:boolean=true;
  constructor(
    public commonHttpService: CommonHttpService,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {
    this.orgquery('','');
  }
  //机构查询
  inpquery($event){
    if(this.inpFlag){
      this.inpFlag=false;
      setTimeout(()=>{
        this.inpFlag=true;
        let orgAddId=document.getElementById('orgAddId').getElementsByTagName('input')[1].value
        let orgAddName=document.getElementById('orgAddName').getElementsByTagName('input')[1].value
        // this.orgquery($event.path[0].value)
        this.orgquery(orgAddId,orgAddName)
      },1000)
    }
  }
  orgquery(orgId,orgName){
    let param = {
      orgId:orgId,
      orgName:orgName,
      pageSize: '30'
    }
    this.commonHttpService.queryVagueOrgListLevel(param).subscribe(data => {
      if (data.orgList) {
        this.list = this.commfunc.listToLabelvalue(data.orgList, 'orgName', 'orgId');
        this.list1 = this.commfunc.listToLabelvalue(data.orgList, 'orgId', 'orgName');
      }
    })
  }
  
  //onchange事件
  doChose(event) {
    this.orgName = this.returnValue(event.value, 'label', 'value');
  }
  doChose1(event) {
    this.orgId = this.returnValue(event.value, 'value', 'label');
  }

  /**
* 转换值
* @param param  值
* @param colum  字段
*/
  returnValue(param, colum, valueColum) {
    let value: string = "";
    this.list.forEach(element => {
      if (element[colum] === param) {
        value = element[valueColum];
      }
    });
    return value;
  }
  //加入权限确定机构
  query() {
    this.postAssignmentBean.orgId = this.orgName;
    this.postAssignmentBean.orgName = this.orgId;
    this.commonHttpService.queryOrgByCombConditions(this.postAssignmentBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.returnList
        if (data.returnList) {
          this.tableData.forEach(element => {
            this.postAssignmentBean.orgId = element.orgId,
            this.postAssignmentBean.orgName = element.orgName
          });
        }
        this.outValue.emit(this.postAssignmentBean);        
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '查询机构成功' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }

    })
  }

}
